import React from 'react'
import {BrowserRouter as Router,Route,NavLink} from 'react-router-dom';
import api from "../../../api/api";
import { Input, Tabs, Menu, Icon } from 'antd';
import './index.css'

const TabPane = Tabs.TabPane;
const SubMenu = Menu.SubMenu;
const Search = Input.Search;


class Slider extends React.Component {
    constructor(props){
        super(props)

    }
    
    render() {
        return (
            <div id='SliderVideo'>
                <div className="search" >
                    <Search
                        placeholder="搜索"
                        onSearch={this.getSearch}
                        style={{ width: '91.95%'}}
                    />
                </div>
                <div className="listLink">
                   <NavLink to='/Video/Index' exact  activeClassName='link-active'>
                    <p>全部文章</p>
                   </NavLink>
                   <NavLink to='/Video/Index/ExamineDept'>
                        <p>待审核</p>
                        <NavLink to='/Video/Index/ExamineDept' activeClassName='link-active'>
                            <p>部门审核</p>
                        </NavLink>
                        <NavLink to='/Video/Index/ExamineColumn' activeClassName='link-active'>
                            <p>栏目审核</p>
                        </NavLink>
                    </NavLink>
                   <NavLink to='/Video/Index/VideoPage' activeClassName='link-active'>
                    <p>视频页管理</p>
                   </NavLink>
                   <NavLink to='/Video/Index/Column' activeClassName='link-active'>
                    <p>栏目管理</p>
                   </NavLink>
                 
                </div>
            </div>
        )
    }
}
const Styles={
    submenu:{
        'paddingLeft':'16px'
    }
}
export default Slider