import React from 'react';
import { withRouter, Switch, Redirect,Route } from 'react-router-dom'
import LoadableComponent from '../../../utils/LoadableComponent'

import { Breadcrumb } from 'antd';
import './index.css'
// import { p } from 'gsap/RoundPropsPlugin';
const VideoPage = LoadableComponent(()=>import('../VideoPage/VideoPage'))
const Column = LoadableComponent(()=>import('../Column/Column'))
const All = LoadableComponent(()=>import('../All/All'))
const ExamineColumn = LoadableComponent(()=>import('../ExamineColumn/Examine'))
const ExamineDept = LoadableComponent(()=>import('../ExamineDept/Examine'))


class Content extends React.Component {
    constructor(props){
        super(props)

    }
    render() {
        return (
            <div id="content">
               <Switch>
                    <Route   path='/Video/Index/VideoPage' component={VideoPage}></Route>
                    <Route exact path='/Video/Index/Column' component={Column}></Route>
                    <Route exact path='/Video/Index/ExamineColumn' component={ExamineColumn}></Route>
                    <Route exact path='/Video/Index/ExamineDept' component={ExamineDept}></Route>

                    <Route exact path='/Video/Index' component={All}></Route>
                </Switch>
            </div>
        )
    }
}
export default Content